html {
  text-align: left;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  line-height: 14.06px;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.picture {
  width: 40px;
  height: 40px;
  padding: 0;
}

:root {
  --blue-color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.link_style {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
  position: relative;
}

.is-active, .link_style:hover {
  color: var(--blue-color);
}

.nav_list {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

